<script>
import Header from '@/components/Layout/Header'
import Footer from '@/components/Layout/Footer'

export default {
  name: 'home',
  components: {
    Header, Footer
  },
  data () {
    return {
      activeName: 'a',
      name: '',
      nameb: '',
      input: ''
    }
  },
  methods: {
    mOver () {
      var pic = document.getElementById('pic')
      var width = parseInt(pic.style.width)
      var i = width
      var count = 0
      // console.log(width)
      var timer = setInterval(function () {
        count++
        i++
        pic.style.width = i + '%'
        if (count === 20) {
          clearInterval(timer)
        }
      }, 18)
    },
    normalImg (obj) {
      var pic = document.getElementById('pic')
      var width = parseInt(pic.style.width)
      var count = 0
      if (width === 10) {
        alert('图片已最小!!')
        return false
      }
      // console.log(width)
      var timer2 = setInterval(function () {
        count++
        width--
        pic.style.width = width + '%'
        if (count === 20) {
          clearInterval(timer2)
        }
      }, 18)
    },
    handleClick (tab) {
      this.name = tab.name
      const demoid = document.getElementById(this.name)
      const demoid1 = document.getElementById(this.name + '1')
      demoid.addEventListener('mousemove', function (e) {
        demoid.style.display = 'none'
        demoid1.style.display = 'block'
      })
    },
    overa: function () {
      const obj = document.getElementById('a')
      const obj1 = document.getElementById('a1')
      obj.addEventListener('mousemove', function (e) {
        obj.style.display = 'none'
        obj1.style.display = 'block'
      })
    },
    outa: function () {
      // console.log(this.name)
      if (this.name === 'a') {
        const obj = document.getElementById('a')
        const obj1 = document.getElementById('a1')
        obj1.addEventListener('mouseout', function (e) {
          obj1.style.display = 'none'
          obj.style.display = 'block'
        })
